@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,html {
  font-family: "Montserrat", sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-banner{
    width: 65%;
    margin-left: 30px;
    margin-top: 25px;
    z-index: 0;
}
.movie-list {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: center;
}

.movie-title {
  font-weight: 600;
  font-size: 19px;
  width: 300px;
  margin-bottom: 50px;
}

a .movie-title {
  color: #2d2d2d;
}

.movie-item a:hover {
  text-decoration: none;
}

.movie-item {
  margin: 25px 15px;
}

.movie-vote {
  border-radius: 5px;
  padding: 5px;
  font-weight: 600;
  color: #fff;
}

.main-badge {
  margin: 16px 15px;
  text-align: left;
}

.upcoming-badge {
  color: #fff;
  font-size: 16px;
  background-color: #006563;
  margin: 0 30px;
  display: inline;
  font-weight: 700;
  padding: 4px 40px;
  width: 100%;
  max-width: 170px;
}

.main-playing {
  margin: 16px 15px;
  text-align: left;
  display: flex;
}

.city{
  width: 250px;
  margin: 0;
  padding: 0;
}

.playing-badge {
  color: #fff;
  font-size: 16px;
  background-color: #006563;
  margin: 0 30px;
  display: inline;
  font-weight: 700;
  padding: 4px 20px;
  width: 100%;
  max-width: 170px;
  text-align: center;
}

.allUpcoming {
  padding: 6px 25px;
  display: inline-block;
  background-color: #006563;
  color: #ffffff;
  font-size: 13px;
  border-radius: 6px;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;
}

.form-inline {
    margin-left: auto;
}

.form-inline .form-control{
    border-radius: 15px;
}
.main-menubar{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

li {
  list-style: none;
  font-size: 14px;
}

.link-menu::before {
  content: "";
  width: 24px;
  height: 24px;
  display: block;
  position: absolute;
}

.link-menu {
  line-height: 24px;
  color: #006563;
  margin: 0 40px;
}

.link-menu:hover {
  color: #cfab7a;
  transition: all 0.3s ease-in-out;
}

.link-menu.now-playing::before {
  background-image: url(data:image/png;base64,);
  background-position: -24px 0;
  margin-left: -25px;
  margin-bottom: -10px;
}

.link-menu.upcoming::before {
  background-image: url(data:image/png;base64,);
  background-position: -48px 0;
  margin-left: -25px;
  margin-bottom: -10px;
}

.link-menu.theaters::before {
  background-image: url(data:image/png;base64,);
  background-position: -72px 0;
  margin-left: -25px;
  margin-bottom: -10px;
}

.link-menu.promotions::before {
  background-image: url(data:image/png;base64,);
  background-position: -96px 0;
  margin-left: -25px;
  margin-bottom: -10px;
}

.link-menu.info::before {
  background-image: url(data:image/png;base64,);
  background-position: -120px 0;
  margin-left: -10px;
  margin-top: 9px;
}

.dropdown-toggle {
  color: #006563;
}

.dropdown-toggle:hover {
  color: #cfab7a;
  transition: all 0.3s ease-in-out;
}

.partner {
  float: right;
}

.partner{
  float: right;
  border-left: 1px solid #c0c0c2;
  height: 38px;
  margin-left: 50px;
}

.partner a{
    margin: 0 30px;
}
.main-footer {
  background-color: #006563;
  padding: 50px;
}

.footer-left {
  float: left;
  list-style: none;
  display: flex;
  color: #fff;
  margin-left: -30px;
}

.footer-right {
  display: flex;
  float: right;
  list-style: none;
}

.footer-right li a {
  color: #fff;
  width: 34px;
  height: 34px;
  display: block;
  font-size: 16px;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 50%;
  text-align: center;
  transition: all 0.3s ease-in-out;
  padding-top: 2px;
}

.footer-right li a:hover{
  color: #006563;
  background-color: #fff;
}

.ordered-footer {
  list-style: none;
  display: flex;
  font-size: 15px;
  font-weight: 300;
}

.footer-text {
  text-align: left;
  font-size: 11px;
  color: azure;
  font-family: serif;
  width: 860px;
}

li a {
  color: #fff;
  margin: 0 15px;
}

li a:hover {
  color: #cfab7a;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

